import React, {Component} from 'react';
import styles from "./Main.module.scss";
import Cut from "../Cut/Cut";

class Main extends Component {
    render() {
        return (
            <main id={styles.main}>
                <div className={styles.background}>
                    <Cut text="教育背景"/>
                    <ul>
                        <li>
                            <p>西南科技大学 2012.09-2016.12 建筑经济管理 本科</p>
                        </li>
                        <li>
                            <p>四川城市职业学院 2012.09-2015.06 工程造价 大专</p>
                        </li>
                    </ul>
                </div>
                <div className={styles.work}>
                    <Cut text="工作经历"/>
                    <ul>
                        <li>
                            <h2>成都精湛职业学校</h2>
                            <h4>市场运营 全职 2019.02 - 2019.12</h4>
                            <p>鉴定技术工人，招生成人自考</p>
                        </li>
                        <li>
                            <h2>完美世界(北京)网络技术有限公司</h2>
                            <h4>游戏内违规处理/IM 全职 2016.06 - 2018.08</h4>
                            <p>对玩家游戏中的违规言论进行判断，然后对于该账号或角色进行封号，禁言处理对论坛上的内容进行违规处理</p>
                        </li>
                        <li>
                            <h2>成都市彼索科技有限公司</h2>
                            <h4>游戏在线客服 全职 2015.11 - 2016.04</h4>
                            <p> 解答玩家问题，举行活动，给予玩家一些奖励</p>
                        </li>
                    </ul>
                </div>
                <div className={styles.project}>
                    <Cut text="项目经历"/>
                    <ul>
                        <li>
                            <h2>个人主页</h2>
                            <h4>2019.11 - 至今</h4>
                            <p>通过react和vue技术栈搭建个人主页，先使用nodejs搭建后端，后将后端重新使用springboot重建个人主页地址：mikumirai.github.io</p>
                        </li>
                        <li>
                            <h2>萝卜TD</h2>
                            <h4>2019.11 - 至今</h4>
                            <p>通根据保卫萝卜进行复刻，并在其上增加了地图编辑器，可以实现自定义地图、自定义放塔点、自定义怪物路径、自定义地图背景等功能。</p>
                        </li>
                    </ul>
                </div>
            </main>
        );
    }
}

export default Main;