import React, {Component} from 'react';
import styles from "./Cut.module.scss";

class Cut extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        const {text} = this.props;
        return (
            <div id={styles.cut}>
                <h2>{text}</h2>
            </div>
        );
    }
}

export default Cut;